	module.exports = {"data":{"markdownRemark":{"html":"<p>The JavaScript array method <code>.toString()</code> is used to convert an array into a single string, with each element joined by a comma. There are no parameters for the method.</p>\n<p><strong>Syntax</strong></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>  <span class=\"token keyword\">var</span> arr <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">,</span> <span class=\"token number\">2</span><span class=\"token punctuation\">,</span> <span class=\"token number\">3</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n  arr<span class=\"token punctuation\">.</span><span class=\"token function\">toString</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h2>Usage</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>  <span class=\"token keyword\">var</span> str1 <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">,</span> <span class=\"token number\">2</span><span class=\"token punctuation\">,</span> <span class=\"token number\">3</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">,</span> <span class=\"token number\">5</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">.</span><span class=\"token function\">toString</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>  <span class=\"token comment\" spellcheck=\"true\">// str1 = '1,2,3,4,5';</span>\n  <span class=\"token keyword\">var</span> str2 <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token string\">'1'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'2'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'3'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'4'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">.</span><span class=\"token function\">toString</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>  <span class=\"token comment\" spellcheck=\"true\">// str2 = '1,2,3,4';</span>\n  <span class=\"token keyword\">var</span> str3 <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token string\">'Free'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'Code'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'Camp'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">.</span><span class=\"token function\">toString</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>  <span class=\"token comment\" spellcheck=\"true\">// str3 = 'Free,Code,Camp';</span>\n  <span class=\"token keyword\">var</span> str4 <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token string\">'phone'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'555-6726'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">.</span><span class=\"token function\">toString</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>   <span class=\"token comment\" spellcheck=\"true\">// str4 = 'phone,555-6726';</span>\n  <span class=\"token keyword\">var</span> str5 <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token string\">'August'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'September'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'October'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">.</span><span class=\"token function\">toString</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>  <span class=\"token comment\" spellcheck=\"true\">// str5 = 'August,September,October';</span>\n  <span class=\"token keyword\">var</span> str6 <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token string\">'Words'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'and'</span><span class=\"token punctuation\">,</span> <span class=\"token number\">3</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">.</span><span class=\"token function\">toString</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>  <span class=\"token comment\" spellcheck=\"true\">// str6 = 'Words,and,3,4';</span>\n</code></pre>\n      </div>\n<p>Source : <a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/toString' target='_blank' rel='nofollow'>MDN</a></p>","fields":{"slug":"/javascript/standard-objects/array/array-prototype-tostring/"},"frontmatter":{"title":"Array.prototype.toString"}}},"pathContext":{"slug":"/javascript/standard-objects/array/array-prototype-tostring/"}}

